let baseUrl = ''; //




const token = wx.getStorageSync("token");

export const request = (options = {}) => {
	//Promise
	return new Promise((resolve, reject) => {
		// 
		uni.request({
			header: {
				token
			},
			// url: "http://116.62.178.10:3000" + options.url || '', //API
			url: "http://localhost:3000" + options.url || '', //API
			method: options.method || 'GET', //,GET
			data: options.data || {}, //data,
		}).then(data => {
			let [err, res] = data;
			resolve(res ? res.data : {});
		}).catch(error => {
			reject(error);
		})
	})
}